import { Component } from "react";
import Link from "next/link";
import axios from "axios";

class Robots extends Component {
  constructor(props) {
    super(props);
    this.state = {
      robots: null
    };
  }

  componentDidMount() {
    axios
      .get("https://jsonplaceholder.typicode.com/users")
      .then(response => {
        console.log(response);
        this.setState({ robots: response.data });
      })
      .catch(error => {
        console.log(error, "error");
      });
  }

  handleClick = robotId => {
    const selectedRobot = this.state.robots.find(robot => robot.id === robotId);
    console.log("selectedRobot :", selectedRobot);

    Router.push({
      pathname: `/robot?id=${selectedRobot.id}`,
      query: {
        robot: selectedRobot
      }
    });
  };

  renderRobotsList = () => {
    return this.state.robots.map(robot => {
      return (
        <li key={robot.id}>
          <a onClick={() => this.handleClick(robot.id)}>{robot.name}</a>
        </li>
      );
    });
  };

  render() {
    return (
      <div>
        <h1>Robots</h1>
        <Link href="/">
          <button>Home</button>
        </Link>
        <div>{this.state.robots && this.renderRobotsList()}</div>
      </div>
    );
  }
}

export default Robots;
